import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { CSpinner } from '@coreui/react';

import { HBButton } from 'hbBaseClass';
import { SUCCESS, useApi } from 'store/callApi';
import { useFetching } from 'store/loading';
import config from 'config';
import { ESIGNATURE_STATUS } from 'configs/constants';

import styles from './InitProfile.module.scss';

const { COMPLETE } = ESIGNATURE_STATUS;

interface Props {
  back: any;
  isSignatureCompleted?: boolean;
  isPushedDocusignToS3?: boolean;
}

const InitDone: React.FC<Props> = (props) => {
  const { t } = useTranslation();
  const history = useHistory();
  const { callApi } = useApi();
  const { fetchLoading } = useFetching();

  // props
  const { isSignatureCompleted, isPushedDocusignToS3 } = props;

  // states
  const [isProcessingDocusign, setIsProcessingDocusign] = useState(false);

  useEffect(() => {
    if (!isSignatureCompleted) return;

    setIsProcessingDocusign(true);
    callApi(
      {
        method: 'put',
        api: config.rest.signatureDocusignCompleted(),
        body: {},
        msg: t('errorUnknown'),
      },
      (response: any) => {
        setIsProcessingDocusign(false);
        const { status } = response;
        if (status !== SUCCESS) return;

        history.push({ pathname: '/zur-app', state: 'upload' });
      },
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isSignatureCompleted]);

  /**
   * Handle signature
   */
  const handleSignature = () => {
    if (isProcessingDocusign) return;

    callApi(
      {
        method: 'post',
        api: config.rest.requestDocusign(),
        body: {},
        msg: t('errorUnknown'),
        loading: true,
      },
      (response: any) => {
        const { status, data } = response;
        if (status !== SUCCESS) return;

        const { status: signatureStatus, url: docusignUrl = '' } = data || {};
        if (signatureStatus === COMPLETE) {
          history.push({ pathname: '/zur-app', state: 'upload' });
          return;
        }

        fetchLoading();
        // if nurse have not signature before => signature on docusign
        window.location.href = docusignUrl;
      },
    );
  };

  return (
    <div className={styles.initDone}>
      {isPushedDocusignToS3 ? (
        <p className={styles.msgSignatureSuccess}>Wir überprüfen jetzt Ihre Daten und kontaktieren Sie in Kürze.</p>
      ) : (
        <>
          <h2 className={styles.title}>Daten mit Unterschrift bestätigen</h2>
          <HBButton
            color="petrol"
            btnClassName={`${styles.btn} ${styles.signature}`}
            onClick={handleSignature}
            className={styles.wrap}
          >
            {isProcessingDocusign ? <CSpinner color="light" size="sm" /> : 'Via Docusign unterschreiben'}
          </HBButton>
        </>
      )}
    </div>
  );
};

export default InitDone;
