import React, { useEffect, useRef, useState, useMemo } from 'react';
import { CCard, CCardBody, CContainer, CRow } from '@coreui/react';
import { RouteComponentProps, useHistory } from 'react-router-dom';
import { includes } from 'lodash';
import Intro from './Intro';
import PersonalInformation from './PersonalInformation';
import CardPassport from './CardPassport';
import Certificate from './Certificate';
import CV from './CV';
import InitDone from './InitDone';
import { useSelector } from 'react-redux';
import { SUCCESS } from '../../../store/callApi/actions';
import { useApi } from 'store/callApi';
import config from '../../../config';
import { ProgressLine } from '../../../components';
import Header from '../authContainers/Header';
import styles from './InitProfile.module.scss';
import { HBButtonSmall, HBModalConfirm } from '../../../hbBaseClass/index';
import { ESIGNATURE_STATUS } from 'configs/constants';

const { DOCUSIGN_CALLBACK_COMPLETED, DOCUSIGN_VIEWING_COMPLETED, COMPLETE } = ESIGNATURE_STATUS;

const defaultData: any = {
  first_name: '',
  last_name: '',
  date_of_birth: undefined,
  gender: '',
  card_number: '',
  card_location: '',
  card_expires: undefined,
  image_front: '',
  image_back: '',
  envelope_status: '',
  files_professional: [],
  files_consultation: [],
  files: { curriculum_vitae: [] },
};

const UpdateProfile: React.FC<RouteComponentProps> = () => {
  const history = useHistory();
  const { callApi } = useApi();
  const auth = useSelector((state: { auth: { status_name: string; group_name: string } }) => state.auth);

  const { location } = history;
  const { search } = location;
  const query = new URLSearchParams(search);
  const signatureStatus = query.get('event');

  const formRef = useRef<any>(null);
  const [showPopupSignout, setShowPopupSignout] = useState(false);
  const isAssistant = auth.group_name === 'assistant';
  const isNurse = auth.group_name === 'nurse';

  const [data, setData] = useState<{
    first_name: string;
    last_name: string;
    date_of_birth?: string;
    gender: number;
    card_number: string;
    card_location: string;
    card_expires?: number;
    image_front: string;
    image_back: string;
    envelope_status?: string;
    files_professional: string[];
    files_consultation: string[];
    files: { curriculum_vitae: string[] };
  }>(defaultData);
  const { envelope_status = '' } = data;

  const [dataNew, setDataNew] = useState({
    values: defaultData,
    setValues: (values: any) =>
      setDataNew((dataNew: any) => {
        return { ...dataNew, values: { ...dataNew.values, ...values } };
      }),
    frontFile: { url: data.image_front, file: null },
    backFile: { url: data.image_front, file: null },
    cardExpires: data.card_expires ? new Date(data.card_expires * 1000) : null,
    filesProfessional: isNurse ? data.files_professional || [] : undefined,
    filesConsultation: isNurse ? data.files_consultation || [] : undefined,
    files: isAssistant ? data.files || {} : undefined,
  });

  if (!auth.status_name?.toLowerCase?.().includes('pending')) history.push('/sign-in');

  // get status signature completed
  const isSignatureCompleted = useMemo(
    () => includes([DOCUSIGN_CALLBACK_COMPLETED, DOCUSIGN_VIEWING_COMPLETED], signatureStatus),
    [signatureStatus],
  );
  const [step, setStep] = useState<number>(isSignatureCompleted ? 4 : 0);

  const uploadMetaData = (body: any, callback: any, callbackFailed?: any) => {
    let newBody = body;
    delete newBody?.envelope_status;
    delete newBody?.signature;

    callApi(
      {
        method: 'post',
        api: (auth.group_name === 'assistant'
          ? config.rest.updateProfileAssistant
          : config.rest.updateProfileDocuments)(),
        body: newBody,
        loading: true,
      },
      (response: any) => {
        const { status, id } = response;
        if (status === SUCCESS) {
          setData(body);
          callback();
        } else {
          callbackFailed?.(id);
        }
      },
    );
  };

  const getProfile = () => {
    callApi(
      {
        method: 'get',
        api: (auth.group_name === 'assistant' ? config.rest.getProfileAssistant : config.rest.getProfileDocuments)(),
      },
      (response: any) => {
        const { status } = response;
        if (status === SUCCESS) {
          const data1 = response.data;
          const data2 = {
            ...data1,
            files_professional: isNurse ? data1.files_professional : undefined,
            files_consultation: isNurse ? data1.files_consultation : undefined,
            files: isAssistant ? data1.files : undefined,
          };
          setData(data2);
          setDataNew((dataNew: any) => {
            return {
              ...dataNew,
              values: data2,
              frontFile: { url: data1.image_front, file: null },
              backFile: { url: data1.image_back, file: null },
              cardExpires: data1.card_expires ? new Date(data1.card_expires * 1000) : null,
              filesProfessional: isNurse ? data1.files_professional || [] : undefined,
              filesConsultation: isNurse ? data1.files_consultation || [] : undefined,
              files: isAssistant ? data1.files || {} : undefined,
            };
          });
        } else history.push('/sign-in');
      },
    );
  };

  useEffect(() => {
    getProfile();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  let Form: any;

  switch (step) {
    case 0:
      Form = <Intro next={() => setStep(1)} data={data} step={0} />;
      break;
    case 1:
      Form = (
        <PersonalInformation
          uploadMetaData={uploadMetaData}
          data={data}
          formRef={formRef}
          next={() => setStep(2)}
          prev={() => setStep(0)}
        />
      );
      break;
    case 2:
      Form = (
        <CardPassport
          {...dataNew}
          uploadMetaData={uploadMetaData}
          data={data}
          setDataNew={setDataNew}
          back={() => setStep(1)}
          next={() => setStep(3)}
        />
      );
      break;
    case 3:
      if (auth.group_name === 'assistant') {
        Form = (
          <CV
            {...dataNew}
            uploadMetaData={uploadMetaData}
            data={data}
            setDataNew={setDataNew}
            next={() => setStep(4)}
          />
        );
      } else {
        Form = (
          <Certificate
            {...dataNew}
            uploadMetaData={uploadMetaData}
            data={data}
            setDataNew={setDataNew}
            back={() => setStep(2)}
            next={() => setStep(4)}
          />
        );
      }
      break;
    case 4:
      Form = (
        <InitDone
          isSignatureCompleted={isSignatureCompleted}
          isPushedDocusignToS3={envelope_status === COMPLETE}
          back={() => setStep(3)}
        />
      );
      break;
  }

  const backStep = (step: any) => {
    switch (step) {
      case 1:
        setStep(0);
        break;
      case 2:
        setStep(1);
        break;
      case 3:
        setStep(2);
        break;
      case 4:
        setStep(3);
        break;
    }
  };

  return (
    <div className={styles.initProfile}>
      <Header />

      <div className={styles.wrapper}>
        <CContainer className={styles.wrapperResContainer}>
          <div className="petrol">
            <HBModalConfirm
              show={showPopupSignout}
              handleClose={() => setShowPopupSignout(false)}
              title="Abmelden"
              content="Sie wollen sich von Ihrem Account abmelden?"
              up="Ja, abmelden"
              upCallback={() => history.push('sign-in')}
              down="Abbrechen"
              downCallback={() => setShowPopupSignout(false)}
            />
          </div>

          <div
            className={`d-flex ${step !== 0 ? 'justify-content-between' : 'justify-content-end'} hb-my-28 ${
              styles.wrapButtons
            }`}
          >
            {step !== 0 && (
              <HBButtonSmall color="petrol" onClick={() => backStep(step)} className={styles.btnSignUp}>
                <i className={`hb-icon-arrow-left ${styles.hbIcon}`} /> Zurück
              </HBButtonSmall>
            )}
            <HBButtonSmall color="petrol" onClick={() => setShowPopupSignout(true)} className={styles.btnSignUp}>
              <i className={`hb-icon-lock-2-opened ${styles.hbIcon}`} /> Abmelden
            </HBButtonSmall>
          </div>

          <CRow className="nopadding">
            <CCard className={[styles.card, styles.cardResContainer]}>
              <CCardBody className={styles.cardResBody}>
                <div className={`${styles.title} ${styles.stepInfo}`}>{`Schritt ${step} von 4`}</div>
                <ProgressLine
                  backgroundColor="var(--petrol-20)"
                  visualParts={[
                    {
                      id: '1',
                      percentage: (step * 100) / 4 + '%',
                      color: 'var(--aquamarin-90)',
                    },
                  ]}
                />
                {Form}
              </CCardBody>
            </CCard>
          </CRow>
        </CContainer>
      </div>
    </div>
  );
};

export default UpdateProfile;
