import React, { useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import config from '../../../config';
import { loadingRequest, loadingSuccess } from '../../../store/loading/actions';
import styles from './InitProfile.module.scss';
import { CCol, CRow } from '@coreui/react';
import { Page } from 'react-pdf';
import { HBButtonFull, HBButtonSmall, HBModalConfirm } from '../../../hbBaseClass';
import { MAX_DOCUMENT_SIZE } from 'configs/constants';
import { useApi } from 'store/callApi';
import { KMSDocument, KMSImg } from 'components';
import { trimFileName } from 'utils';

interface Props {
  uploadMetaData: any;
  data: {
    files: any;
  };
  files: any;
  setDataNew: any;
  back?: any;
  next: any;
}

const CV: React.FC<Props> = (props) => {
  const { uploadMetaData, data, files, setDataNew, next } = props;
  const refProfessional = useRef<any>(null);
  //const refConsultation = useRef<any>(null);
  const dispatch = useDispatch();
  const { callApi } = useApi();
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [show, setShow] = useState(false);

  function onDocumentLoadSuccess({ numPages }: any) {
    setNumPages(numPages);
  }

  const setFilesProfessional = (value: any) =>
    setDataNew((dataNew: any) => {
      return { ...dataNew, files: { curriculum_vitae: value } };
    });

  //   const setFilesConsultation = (value: any) =>
  //     setDataNew((dataNew: any) => {
  //       return { ...dataNew, filesConsultation: value };
  //     });

  const includes = (arr: any[], item: any) => {
    return arr.findIndex((i: any) => i.file_name === item.file_name) !== -1;
  };

  const checkFileSize = (item: any) => item.size <= MAX_DOCUMENT_SIZE;

  const onUploadProfessional = (e: any) => {
    const selectedFiles = e.target.files;
    if (selectedFiles.length === 0) return;
    const oldFiles = [...selectedFiles];
    const filteredValidFiles = oldFiles.filter(checkFileSize);
    if (filteredValidFiles.length !== oldFiles.length) setShow(true);
    const newFiles = filteredValidFiles
      .map((item) => {
        return { file_name: trimFileName(item.name), file: item, link: URL.createObjectURL(item) };
      })
      .filter((item: any) => !includes(files.curriculum_vitae, item));
    setFilesProfessional([...files.curriculum_vitae, ...newFiles].slice(0, 10));
    e.target.value = null;
  };

  const onSubmit = () => {
    if (files === data.files) {
      next();
      return;
    }

    let countDone = 0;

    const arr1: any[] = files.curriculum_vitae.filter((item: any) => !item.file);
    const newFilesProfessional = files.curriculum_vitae.filter((item: any) => item.file);
    //const newFilesConsultation = filesConsultation.filter((item: any) => item.file);
    const total = newFilesProfessional.length;

    const done = () => {
      const newData = {
        ...data,
        files: { curriculum_vitae: arr1 },
        //files_consultation: arr2,
      };
      const next2 = () => {
        setDataNew((dataNew: any) => {
          return { ...dataNew, files: { curriculum_vitae: arr1 } };
        });
        next();
      };
      uploadMetaData({ ...newData, step: 3 }, next2);
    };

    const upload = (files: any, arrLinks: any) => {
      if (files.length > 0) {
        files.forEach((item: any) => {
          callApi(
            {
              api: config.rest.assistantTemporaryUpload(trimFileName(item.file_name)),
              method: 'get',
            },
            (response: any) => {
              console.log('QUY', response);
              arrLinks.push({ ...response.data.documents });
              const myHeaders = new Headers();
              myHeaders.append('x-amz-acl', 'public-read');
              const requestOptions: any = {
                method: 'PUT',
                headers: myHeaders,
                body: item.file,
                redirect: 'follow',
              };
              fetch(response.data?.link, requestOptions)
                .then((response) => response.text())
                .then((result) => {
                  countDone += 1;
                  if (countDone === total) {
                    dispatch(loadingSuccess());
                    done();
                  }
                })
                .catch((error) => {
                  dispatch(loadingSuccess());
                  console.log(error);
                });
            },
          );
        });
      }
    };

    if (total === 0) {
      done();
    } else {
      dispatch(loadingRequest());
      upload(newFilesProfessional, arr1);
      //upload(newFilesConsultation, arr2, 'files_consultation');
    }
  };
  const handleClose = () => {
    setShow(false);
  };

  return (
    <>
      <div className="petrol">
        <HBModalConfirm
          show={show}
          handleClose={handleClose}
          title="Hochladen fehlgeschlagen"
          content="Es kann keine Datei hochgeladen werden, die größer als 50 MB ist."
          up="Verstanden"
          upCallback={handleClose}
        />
      </div>
      <div className={styles.certificate}>
        <h2 className={styles.title}>Lebenslauf</h2>
        <CRow className={styles.document}>
          <CCol xs={12}>
            <div className={styles.professionalDocument}>
              {/* <p className={styles.title}>Lebenslauf</p> */}
              <input
                ref={refProfessional}
                type="file"
                onChange={onUploadProfessional}
                accept="image/x-png,image/jpeg,application/pdf"
                multiple
                hidden
              />

              <div className={styles.btnProfessional} onClick={() => refProfessional.current.click()}>
                <div className={styles.contentNurse}>
                  <div className={styles.uploadDocument}>
                    <i className="hb-icon-book"></i>
                  </div>
                  <span>Lebenslauf hochladen</span>
                </div>
                <div className={styles.iconArrowNurse}>
                  <i className="hb-icon-arrow-right"></i>
                </div>
              </div>

              <div className={styles.pdfNurse}>
                {files?.curriculum_vitae.map((item: any, index: number) => {
                  const splitItems = item.file_name.split('.');

                  return (
                    <div key={index} className={styles.nurseDocumnet}>
                      {splitItems[splitItems.length - 1] === 'pdf' ? (
                        <KMSDocument
                          //file={'https://raw.githubusercontent.com/mozilla/pdf.js/ba2edeae/examples/learning/helloworld.pdf'}
                          file={item.link}
                          onLoadSuccess={onDocumentLoadSuccess}
                        >
                          <Page pageNumber={pageNumber} height={467} />
                        </KMSDocument>
                      ) : (
                        <KMSImg src={item.link} height="467" style={{ width: '100%' }} alt="" />
                      )}

                      <HBButtonSmall
                        color="petrol"
                        onClick={() =>
                          setFilesProfessional(
                            files.curriculum_vitae.filter((_: any, index1: number) => index1 !== index),
                          )
                        }
                        className={styles.nurseClose}
                      >
                        Löschen
                      </HBButtonSmall>
                    </div>
                  );
                })}
              </div>
            </div>
          </CCol>
        </CRow>

        <div className={styles.footer}>
          <div className={styles.wrapperBack}>
            <HBButtonFull
              color="petrol"
              children="Weiter"
              onClick={onSubmit}
              disabled={files.curriculum_vitae.length === 0}
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default CV;
